'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

/*
* The following modules are intentionally not exported: common, napi
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace strided
*/
var strided = {};

/**
* @name base
* @memberof strided
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/strided/base}
*/
import base from './../base';
setReadOnly( strided, 'base', base );

/**
* @name dispatch
* @memberof strided
* @readonly
* @type {Function}
* @see {@link module:@stdlib/strided/dispatch}
*/
import dispatch from './../dispatch';
setReadOnly( strided, 'dispatch', dispatch );

/**
* @name dtypes
* @memberof strided
* @readonly
* @type {Function}
* @see {@link module:@stdlib/strided/dtypes}
*/
import dtypes from './../dtypes';
setReadOnly( strided, 'dtypes', dtypes );


// EXPORTS //

export default strided;
